	module.exports = {"data":{"markdownRemark":{"html":"<p>\n  <span\n    class=\"gatsby-resp-image-wrapper\"\n    style=\"position: relative; display: block; ; max-width: 128px; margin-left: auto; margin-right: auto;\"\n  >\n    <span\n      class=\"gatsby-resp-image-background-image\"\n      style=\"padding-bottom: 100%; position: relative; bottom: 0; left: 0; background-image: url('data:image/png;base64,'); background-size: cover; display: block;\"\n    >\n      <img\n        class=\"gatsby-resp-image-image\"\n        style=\"width: 100%; height: 100%; margin: 0; vertical-align: middle; position: absolute; top: 0; left: 0; box-shadow: inset 0px 0px 0px 400px white;\"\n        alt=\"Read Link Later icon\"\n        title=\"\"\n        src=\"/static/read-link-later-2fbe86961228c8bd6467b3d71f332166-ade98.png\"\n        srcset=\"/static/read-link-later-2fbe86961228c8bd6467b3d71f332166-ade98.png 128w\"\n        sizes=\"(max-width: 128px) 100vw, 128px\"\n      />\n    </span>\n  </span>\n  </p>\n<p><a href=\"https://chrome.google.com/webstore/detail/mffbgadohiiggcibdgmogfdlmackfbhm\">Read Link Later</a> was a Chrome extension that added \"Read Later\" links to tweets on <a href=\"https://twitter.com/\">twitter.com</a> that have URLs.</p>\n<p>I retired Read Link Later in 2012.\nTwitter keeps updating and I needed to allocate time to other projects instead of this extension.</p>\n<p>It handled multiple URLs in a single tweet and updated with the timeline.</p>\n<p>\n  <span\n    class=\"gatsby-resp-image-wrapper\"\n    style=\"position: relative; display: block; ; max-width: 359px; margin-left: auto; margin-right: auto;\"\n  >\n    <span\n      class=\"gatsby-resp-image-background-image\"\n      style=\"padding-bottom: 61.00278551532033%; position: relative; bottom: 0; left: 0; background-image: url('data:image/png;base64,'); background-size: cover; display: block;\"\n    >\n      <img\n        class=\"gatsby-resp-image-image\"\n        style=\"width: 100%; height: 100%; margin: 0; vertical-align: middle; position: absolute; top: 0; left: 0; box-shadow: inset 0px 0px 0px 400px white;\"\n        alt=\"Read Link Later screenshot\"\n        title=\"\"\n        src=\"/static/read-link-later-screenshot-2f21b2f394ef7b80c3d04683b978110f-b77dd.png\"\n        srcset=\"/static/read-link-later-screenshot-2f21b2f394ef7b80c3d04683b978110f-05f10.png 240w,\n/static/read-link-later-screenshot-2f21b2f394ef7b80c3d04683b978110f-b77dd.png 359w\"\n        sizes=\"(max-width: 359px) 100vw, 359px\"\n      />\n    </span>\n  </span>\n  </p>","fields":{"project":{"title":"Read Link Later","subtitle":"Instapaper links on Twitter.com","status":"retired","createdAtTimestamp":"2010-08-03T00:26Z","createdAt":"August 2010","deployedUrl":null,"endedAtTimestamp":"2010-10-21T06:16:22.000Z","endedAt":"October 2010","sourceUrl":"https://github.com/adamstegman/read-link-later"}}},"site":{"siteMetadata":{"title":"Adam Stegman"}}},"pathContext":{"slug":"/projects/read-link-later"}}

